import React, { Component } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import Header from './components/layout/header';
import Todos from './components/Todos';
import AddTodo from './components/AddTodo';
import About from './components/pages/About';
import axios from 'axios';


import './App.css';


class App extends Component {
    state = {
        todos: [], // todos is an empty array in the state. Which later you will put the res.data from the json placeholder in.
        isDeleted: false
    };

    componentDidMount() {
        axios.get('https://jsonplaceholder.typicode.com/todos?_limit=10')
            .then(res => this.setState({ todos: res.data }))
    };

    //Toggle Complete
    markComplete = (id) => {
        this.setState({todos: this.state.todos.map(todo => {
            if(todo.id === id){
                todo.completed = !todo.completed;
            }
            return todo;
            })});
    };

    // DeleteTodo
    delTodo = (id) => {
        axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
            .then(res => this.setState({ todos: [...this.state.todos.filter(todo => todo.id !== id)] }));


    };

    // Add Todo
    addTodo = (title) => {
        axios.post('https://jsonplaceholder.typicode.com/todos', {
            title: title,
            completed : false
        })
            .then(res => this.setState({ todos: [...this.state.todos, res.data] }));

    }

    render() {
        return (
            <Router>
                <div className="App">
                    <div className="container">
                        {/*Because you have two components you have to use "render props". instead of "props". to make sure both components to get loaded*/}
                        <Header />
                    <Route exact path="/" render={props => (
                        <React.Fragment>
                            <AddTodo addTodo={this.addTodo}/>
                            < Todos todos={this.state.todos} markComplete={this.markComplete}
                                    delTodo={this.delTodo} />
                        </React.Fragment>
                    ) }/>
                    <Route path="/about" component={About} />
                    </div>
                </div>
            </Router>
        );
    }
}

export default App;
